<template>
  <div class="download_page" v-loading="loading">
  </div>
</template>

<script>
import { getDownloadLink } from '@/api/common'

export default {
  name: "AppDownload",
  data() {
    return {
      downloadLink: '',
      loading: false,
    };
  },
  components: {

  },
  computed: {
	app() { return this.$store.getters.app },
	user() { return this.$store.getters.user },
    localSign() {
      return window.localStorage.getItem('sign');
    },
    localCode() {
      return window.localStorage.getItem('code');
    },
  },
  methods: {
    
  },
  mounted: function() {
      this.loading = true;

      if((this.localSign || this.localCode || this.app.domain.invite_code) && this.user.roles.indexOf('user') == -1) {
        this.$message.error('请先注册或登录账号');
        this.$router.push('/auth/register');
        return ;
      }

      getDownloadLink({
          t: this.app.isAndroid ? 'android' : 'ios'
      }).then(json => {
        this.loading = false;
          if(json.code == 200) {
              window.location.href = json.list;
            //   this.downloadLink = 'itms-services://?action=download-manifest&url=' + json.list;
          }
      })
  }
};
</script>


<style lang="scss">

.download_page {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

   
</style>